@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
* {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
}

a {
  text-decoration: none;
  color: rgb(230, 227, 109);
  padding: 3px;
}

body {
  font-family: "Rubik", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(64, 65, 58);
  color: rgb(250, 242, 242);
  max-width: 1100px;
  margin: 0 auto;
}

.App{padding:20px;text-align:left}.won-card{background-color:#4da375}.lost-card{background-color:#a34d5d}.no-match-played-card{background-color:#d7f178;padding:20px;border-style:dotted}

.MatchSummaryCard{display:grid;height:310px;padding:10px;margin-bottom:1px;border-style:dotted}.MatchSummaryCard .match-date,.MatchSummaryCard .team-name,.MatchSummaryCard .toss-winner-descision,.MatchSummaryCard .match-winner-result-margin,.MatchSummaryCard .player-of-the-match,.MatchSummaryCard .match-venue,.MatchSummaryCard .match-umpires{margin-top:5px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr;grid-column-gap:20px;grid-row-gap:20px}.TeamPage .team-name-section{grid-column:span 2;justify-self:start;margin-top:10px;color:white}.TeamPage .team-name{font-size:2rem;margin-bottom:20px}.TeamPage .team-image{grid-column:span 1;align-self:end;border:2px solid #ddd;border-radius:4px;padding:3px;width:210px}.TeamPage .win-loss-section{justify-self:center;width:210px;grid-column:span 1;padding:30px;text-align:center}.TeamPage .last-match-summary-section{grid-column:span 4;margin-bottom:0 px;border-style:dotted}.TeamPage .match-summary-vs{grid-column:span 4;grid-row-start:auto;height:-webkit-min-content;height:min-content}.TeamPage .match-summary-section{grid-column:span 1}.TeamPage .more-section{grid-column:span 0.5;align-self:center;font-size:1rem;justify-self:start}.summary-card{height:30px}

.LastestMatchSummaryCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:1px}.LastestMatchSummaryCard .latest-match-additional-detail,.LastestMatchSummaryCard .match-date,.LastestMatchSummaryCard .team-name,.LastestMatchSummaryCard .toss-winner-descision,.LastestMatchSummaryCard .match-winner-result-margin,.LastestMatchSummaryCard .player-of-the-match,.LastestMatchSummaryCard .match-venue,.LastestMatchSummaryCard .match-umpires{margin-top:5px}.LastestMatchSummaryCard .latest-match-additional-detail{text-align:right}.LastestMatchSummaryCard .latest-match-additional-detail>p{margin-bottom:20px}

.MatchDetailsCard{padding:10px;margin-bottom:1px;height:auto;border-style:dotted}.MatchDetailsCard .match-date,.MatchDetailsCard .team-name,.MatchDetailsCard .toss-winner-descision,.MatchDetailsCard .match-winner-result-margin,.MatchDetailsCard .player-of-the-match,.MatchDetailsCard .match-venue,.MatchDetailsCard .match-umpires{margin-top:5px}.MatchDetailsCard .vs{font-size:1rem;text-align:start}

.MatchPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr;grid-column-gap:20px;grid-row-gap:20px}.MatchPage .latest-match-from-year{font:100;grid-column:span 2;justify-self:start;align-self:center;color:whitesmoke}.MatchPage .team-image{grid-column:span 2;align-self:end;justify-self:end;border:2px solid #ddd;border-radius:4px;padding:3px;width:210px}.MatchPage .year-selector-section{grid-column:span 1;padding-top:20px;justify-self:start}.MatchPage .match-detail-section{grid-column:span 3;align-self:center;padding-top:2px}.MatchPage .match-detail-card-section{padding-top:20px}.MatchPage .no-match-played-section{padding:20px}

.YearSelector .year-section{justify-self:center;padding:2px;list-style-type:none}

.TeamStatistics{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr;grid-column-gap:20px;grid-row-gap:20px}.TeamStatistics .team-name-section{grid-column:span 2;text-align:start;align-self:center;margin-top:5px}.TeamStatistics .team-image{grid-column:span 2;align-self:start;justify-self:end;border:2px solid #ddd;border-radius:4px;padding:3px;width:210px}.TeamStatistics .win-vs-loss{grid-column:span 1}.TeamStatistics .win-loss-count-section{text-align:right}.TeamStatistics .win-loss-count-section>p{margin-right:4px}.TeamStatistics .win-loss-count-section>h4{margin-right:4px}.TeamStatistics .pie-chart-section{margin-top:4px;margin-left:100px;margin-right:4px;margin-bottom:4px;border-style:hidden}.TeamStatistics .stat-teams-section-card{background-color:#515555;border:1px;border-style:dashed}

.AllTeams{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr;grid-column-gap:2px;grid-row-gap:20px}.AllTeams .ipl-teams-section{font-size:3rem;margin-bottom:20px;text-align:justify;align-self:start;grid-column:span 2;margin-top:5px;justify-self:center}.AllTeams .team-image{grid-column:span 2;align-self:start;border:2px solid #ddd;border-radius:4px;padding:3px;width:210px;justify-self:center}.AllTeams .all-teams-section{width:250px;justify-self:start;height:250px}.AllTeams .team-name-section{width:210px;text-align:end;margin-top:10px;margin-bottom:2px;margin-left:5px;margin-right:5px}.AllTeams .win-loss-count-section{text-align:start;justify-self:right;margin-top:5px}.AllTeams .pie-chart-section{margin-top:4px;margin-left:110px;margin-right:5px;margin-bottom:4px;border-style:hidden}.AllTeams .stat-teams-section-card{background-color:#353b3b;border:1px;border-style:dashed}.AllTeams .played-won-lost-section{padding:3 px}

.BallByBallMatchDetails{display:grid;grid-template-columns:1fr 1fr 1fr;padding:10px;margin-bottom:1px}.BallByBallMatchDetails .t1-vs-t2-header-section{grid-column:span 3;align-self:baseline;justify-self:center}.BallByBallMatchDetails .teamname-teamimage-section-1{grid-column:span 1;padding:10px;grid-column:span 1;align-self:center;justify-self:center;border-style:solid;width:240px}.BallByBallMatchDetails .vs-section{padding:10px;grid-column:span 1;align-self:center;justify-self:center}.BallByBallMatchDetails .teamname-teamimage-section-2{padding:10px;grid-column:span 1;align-self:center;justify-self:center;border-style:solid;width:240px}.BallByBallMatchDetails .team-image{grid-column:span 1;align-self:start;border:2px solid #ddd;border-radius:4px;padding:3px;width:210px;justify-self:center}.BallByBallMatchDetails .team-name{padding:10px;-webkit-align-self:center;align-self:center;justify-self:center}.BallByBallMatchDetails .vs-image{grid-column:span 1;align-self:start;padding:3px;width:210px;justify-self:center}.BallByBallMatchDetails .ball-by-ball-main-section{grid-column:span 3;align-self:start;padding:20px;justify-self:center}

.BallByBallDetailsCard{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr;border-style:dotted;padding:10px;font-family:"Rubik", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#40413a;color:#fff}.BallByBallDetailsCard .score-section{grid-column:span 2;justify-self:start;align-self:start;padding:10px}.BallByBallDetailsCard .bowl-detail-section{grid-column:span 2;justify-self:start;align-self:start;padding:10px}.BallByBallDetailsCard .over-highlight-section{grid-column:span 1;text-align:center;justify-self:center;align-self:center;padding:2px;height:10 px;width:100px}.BallByBallDetailsCard .no-match-played-card{background-color:#000000;padding:20px;border-style:dotted}.BallByBallDetailsCard .out-card{background-color:#e73414;padding:20px;border-style:dotted}

